.button{padding-left:0.5rem;padding-right:0.5rem;padding-top:0.25rem;padding-bottom:0.25rem;font-weight:600;font-size:0.9rem;color:white;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:-ms-inline-flexbox;display:inline-flex}.button.default{background:#228be6}.button.default:hover{background:#339af0}.button.default:active{background:#228be6}.button.gray{background:#495057}.button.gray:hover{background:#868e96}.button.gray:active{background:#495057}.button.outline{border:2px solid white;border-radius:2px}.button.outline:hover{background:white;color:#228be6}.button.outline:active{background:rgba(255,255,255,0.85);border:2px solid rgba(255,255,255,0.85)}.button:hover{-webkit-box-shadow:0 3px 6px rgba(0,0,0,0.08),0 3px 6px rgba(0,0,0,0.115);box-shadow:0 3px 6px rgba(0,0,0,0.08),0 3px 6px rgba(0,0,0,0.115)}.button.disabled{background:#ced4da;color:#868e96;cursor:default}.button.disabled:hover,.button.disabled:active{-webkit-box-shadow:none;box-shadow:none;background:#ced4da}.button+.button{margin-left:0.5rem}



